/* === Nunito Sans - regular */
@font-face {
	font-family: 'Nunito Sans';
	font-style: normal;
	font-weight: 400;
	src: url("./font/NunitoSans-Regular.eot");
	src: local("Nunito Sans Regular"),
		local("NunitoSans-Regular"),
		url("./font/NunitoSans-Regular.eot") format("embedded-opentype"),
		url("./font/NunitoSans-Regular.woff2") format("woff2"),
		url("./font/NunitoSans-Regular.woff") format("woff"),
		url("./font/NunitoSans-Regular.ttf") format("truetype"),
		url("./font/NunitoSans-Regular.svg") format("svg");
}

/* === Nunito Sans - 600 */
@font-face {
	font-family: 'Nunito Sans';
	font-style: normal;
	font-weight: 600;
	src: url("./font/NunitoSans-SemiBold.eot");
	src: local("Nunito Sans SemiBold"),
		local("NunitoSans-SemiBold"),
		url("./font/NunitoSans-SemiBold.eot") format("embedded-opentype"),
		url("./font/NunitoSans-SemiBold.woff2") format("woff2"),
		url("./font/NunitoSans-SemiBold.woff") format("woff"),
		url("./font/NunitoSans-SemiBold.ttf") format("truetype"),
		url("./font/NunitoSans-SemiBold.svg") format("svg");
}

/* === Nunito Sans - 700 */
@font-face {
	font-family: 'Nunito Sans';
	font-style: normal;
	font-weight: 700;
	src: url("./font/NunitoSans-Bold.eot");
	src: local("Nunito Sans Bold"),
		local("NunitoSans-Bold"),
		url("./font/NunitoSans-Bold.eot") format("embedded-opentype"),
		url("./font/NunitoSans-Bold.woff2") format("woff2"),
		url("./font/NunitoSans-Bold.woff") format("woff"),
		url("./font/NunitoSans-Bold.ttf") format("truetype"),
